/* Reset default margin and padding */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Lato', sans-serif;
  background-color: #f0f9f4; /* Soft greenish background */
  color: #333;
  line-height: 1.6;
  font-size: 16px;
  padding: 0;
}

/* General heading styles */
h1, h2, h3 {
  color: #2c3e50; /* Dark Gray for all headings */
  font-weight: 700;
}

/* Links */
a {
  color: #27ae60; /* Green links */
  text-decoration: none;
  transition: color 0.3s ease;
}

a:hover {
  color: #1f8c4a; /* Darker green for hover */
}

/* Navbar */
nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #ffffff; /* White background for navbar */
  padding: 20px 40px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

nav .logo img {
  width: 150px;
  height: auto;
}

nav ul {
  display: flex;
  list-style-type: none;
}

nav ul li {
  margin: 0 20px;
}

nav ul li a {
  color: #3498db; /* Blue links */
  font-size: 18px;
  font-weight: 600;
  transition: color 0.3s ease;
}

nav ul li a:hover {
  color: #27ae60; /* Green on hover */
}

/* Footer */
footer {
  background-color: #ffffff;
  text-align: center;
  padding: 20px;
  border-top: 1px solid #ddd;
}

footer p {
  color: #333;
}

/* Form Styles */
form {
  background-color: #ffffff;
  padding: 35px;
  border-radius: 10px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 450px;
  margin: 50px auto;
}

form label {
  display: block;
  font-weight: 600;
  margin-bottom: 10px;
  color: #333;
}

form input {
  width: 100%;
  padding: 12px;
  margin-bottom: 20px;
  border: 1px solid #ddd;
  border-radius: 8px;
  font-size: 16px;
}

form input:focus {
  border-color: #27ae60; /* Green focus border */
  outline: none;
}

form button {
  width: 100%;
  padding: 14px;
  background-color: #27ae60;
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

form button:hover {
  background-color: #2ecc71; /* Light green on hover */
}

form button:disabled {
  background-color: #ddd;
  cursor: not-allowed;
}

/* Profile Page */
.profile-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 40px;
  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
}

.profile-container h1 {
  font-size: 2.5rem;
  color: #27ae60; /* Green heading */
  margin-bottom: 20px;
}

.profile-container p {
  font-size: 1.125rem;
  color: #555;
  line-height: 1.8;
}

.profile-container button {
  background-color: #e74c3c;
  color: white;
  border-radius: 8px;
  padding: 12px 24px;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.profile-container button:hover {
  background-color: #c0392b; /* Darker red on hover */
}

/* About Us Page */
.about-us {
  text-align: center;
  padding: 60px 20px;
  background-color: #ffffff;
}

.about-us h1 {
  font-size: 3rem;
  color: #27ae60; /* Green heading */
  font-weight: 700;
  margin-bottom: 20px;
}

.about-us h2 {
  font-size: 2.5rem;
  color: #3498db; /* Blue subheading */
  font-weight: 700;
  margin-top: 40px;
}

.about-us p {
  font-size: 1.125rem;
  color: #555;
  line-height: 1.8;
  margin-bottom: 30px;
}

.about-us button {
  background-color: #3498db; /* Blue button */
  color: white;
  padding: 12px 24px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s ease;
}

.about-us button:hover {
  background-color: #2980b9; /* Darker blue on hover */
}

/* Team Section */
.team {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 40px;
  margin-top: 40px;
}

.team-member {
  width: 220px;
  text-align: center;
  padding: 25px;
  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

.team-member:hover {
  transform: translateY(-10px); /* Hover effect for dynamic feel */
}

.team-member img {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  margin-bottom: 15px;
  object-fit: cover;
}

.team-member h3 {
  font-size: 1.2rem;
  font-weight: 700;
  color: #2c3e50;
}

.team-member p {
  font-size: 0.9rem;
  color: #555;
  margin-bottom: 10px;
}

.team-member .linkedin-logo {
  width: 25px;
  height: 25px;
  transition: opacity 0.3s ease;
}

.team-member .linkedin-logo:hover {
  opacity: 0.8;
}

/* Progress Bar */
.progress-bar {
  width: 100%;
  background-color: #ddd;
  border-radius: 6px;
  height: 8px;
  margin-top: 20px;
}

.progress-bar-filled {
  height: 100%;
  background-color: #27ae60; /* Green progress */
  border-radius: 6px;
  transition: width 0.3s ease;
}

/* Responsive Design */
@media (max-width: 768px) {
  nav {
    flex-direction: column;
    padding: 15px 20px;
  }

  nav ul {
    flex-direction: column;
    align-items: center;
    margin-top: 20px;
  }

  .team {
    flex-direction: column;
    align-items: center;
  }

  .team-member {
    width: 80%;
    max-width: 300px;
    margin-bottom: 30px;
  }

  .profile-container {
    width: 90%;
    padding: 30px;
  }

  .about-us {
    padding: 40px 15px;
  }

  form {
    max-width: 90%;
  }
}
